<template>
  <div>
    <slot name="before"></slot>
    <div :id="elId"
      :style="style"></div>
    <slot name="after"></slot>
  </div>
</template>

<script>
// 
import echarts from 'echarts/lib/echarts'
import 'echarts/lib/chart/bar'
import 'echarts/lib/chart/line'
// 
import 'echarts/lib/component/legend'
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/title'
import 'echarts/lib/component/toolbox'

//uuid
import uuidv1 from 'uuid/v1'
export default {
  name: 'echartbar',
  props: {
    lineechartbar: {
      type: Object,
      default() {
        return {
          width: '100%',
          height: '300px',
          color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
            { offset: 0, color: '#ffffff' },
            { offset: 1, color: '#ecf4ff' }
          ]),
          // {
          //   type: [String, Object],
          //   default() {
          //     return new echarts.graphic.LinearGradient(0, 0, 0, 1, [
          //       { offset: 0, color: "#ffffff" },
          //       { offset: 1, color: "#ecf4ff" }
          //     ]);
          //   }
          // },
          title: [''],
          xAxisDate: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
          seriesData: [10, 52, 200, 334, 390, 330, 220]
        }
      }
    }
  },
  data() {
    return {
      elId: '',
      chartObj: {},
      option: {}
    }
  },
  created() {
    this.elId = uuidv1() //id
    console.log(this.lineechartbar)
  },
  computed: {
    style() {
      return {
        height: this.lineechartbar.height,
        width: this.lineechartbar.width
      }
    }
  },
  watch: {
    lineechartbar: {
      handler(val) {
        this.option.series[0].data = this.lineechartbar.seriesData
        this.option.xAxis[0].data = this.lineechartbar.xAxisDate
        this.chartObj = echarts.init(document.getElementById(this.elId))
        this.chartObj.setOption(this.option)
      },
      deep: true
    }
  },
  mounted() {
    const mytextStyle = {
      color: '#466791', //
      fontStyle: 'normal', //italic  oblique
      fontWeight: 'normal', //bold   bolder   lighter  100 | 200 | 300 | 400...
      fontFamily: 'MicrosoftYaHei', //
      fontSize: 14 //
    }
    const mylineStyle = {
      color: '#000', //'rgb(128, 128, 128)''rgba(128, 128, 128, 0.5)'
      // shadowColor: "red", //
      type: 'dotted', //soliddasheddotted
      width: 2, //
      opacity: 0.06 // 0  1  0 
    }
    const mainlineStyle = {
      width: 3,
      shadowColor: ' rgba(169, 207, 255, 0.8)',
      shadowBlur: 10,
      shadowOffsetY: 10
    }
    const itemLineStyle = {
      // 
      normal: {
        color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [
          { offset: 0, color: '#ffd964' },
          { offset: 1, color: '#fc965f' }
        ])
      }
    }
    const itemBarStyle = {
      // 
      normal: {
        barBorderRadius: 3,
        color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
          { offset: 0, color: '#ff5252' },
          { offset: 1, color: '#fbba81' }
        ])
      },
      emphasis: {
        // areaColor: "#ff0000",
        label: {
          color: '#466791'
        },
        color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
          { offset: 0, color: '#ff5252' },
          { offset: 1, color: '#fbba81' }
        ])
      }
    }
    const itemBarStyle2 = {
      // 
      normal: {
        barBorderRadius: 3,
        color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
          { offset: 0, color: '#00d0a6' },
          { offset: 1, color: '#46eabd' }
        ])
      },
      emphasis: {
        // areaColor: "#ff0000",
        label: {
          color: '#466791'
        },
        color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
          { offset: 0, color: '#00d0a6' },
          { offset: 1, color: '#46eabd' }
        ])
      }
    }

    const itemDataStyle = {
      normal: {
        label: {
          show: true, //
          position: 'top', //
          formatter: '{c} %',
          textStyle: {
            //
            color: '#c3cdde',
            fontSize: 14
          }
        }
      }
    }
    Object.assign(itemBarStyle)
    const legendData = []
    legendData.push(this.title)
    this.option = {
      color: this.color,
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          // 
          shadowStyle: {
            color: 'rgba(98,167,255,0)'
          },
          type: 'shadow' // 'line' | 'shadow'
        }
      },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
      },
      legend: {
        // orient: "vertical",
        // y: "top",
        itemWidth: 10,
        itemHeight: 10
        // left: "auto",
        // top: "0",
        // right: "auto",
        // bottom: "auto",
        // textStyle: mytextStyle,
        // data: this.legendData
      },
      xAxis: [
        {
          type: 'category',
          data: this.lineechartbar.xAxisDate,
          axisLine: false,
          splitLine: false,
          axisLabel: {
            textStyle: mytextStyle
          }
          // axisTick: {
          //   alignWithLabel: true
          // }
        }
      ],
      yAxis: [
        {
          type: 'value',
          axisLine: false,
          splitLine: {
            // grid 
            lineStyle: mylineStyle
          },
          axisLabel: {
            textStyle: mytextStyle,
            formatter: '{value}'
          }
        }
      ],
      series: [
        // 
        {
          name: this.lineechartbar.title[0],
          type: 'line',
          smooth: true,
          itemStyle: itemLineStyle,
          lineStyle: mainlineStyle,
          data: this.lineechartbar.seriesData
        }
      ]
    }
    this.chartObj = echarts.init(document.getElementById(this.elId))
    this.chartObj.setOption(this.option, true)
  }
}
</script>
<style scoped></style>
